import React from "react";
import { useTranslation } from "react-i18next";

import NavBar from "../../../components/NavBar/NavBar";
import StepCard from "../../../components/StepCard/StepCard";

import FmRequirements from "./FmRequirements/FmRequirements";
import WhatFannieMae from "./WhatFannieMae/WhatFannieMae";
import WhatFMDoes from "./WhatFMDoes/WhatFMDoes";
import ApplyFMLoan from "./ApplyFMLoan/ApplyFMLoan";

import "./FannieMaeLoan.css";

function FannieMaeLoan() {
  const { t } = useTranslation();
  return (
    <div>
      <NavBar />
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-7 center">
            <h4 className="header-text-1">{t("fmLoan.head")}</h4>
            <h3 className="header-text-2">{t("fmLoan.sub_1")}</h3>
            <p className="header-text-3">{t("fmLoan.sub_2")}</p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5">
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#what-fm-loan",
                topTxt: t("fmLoan.section_1.head"),
                subTxt: t("fmLoan.section_1.sub"),
                imgSrc: "/images/loan-guide/s-1.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fm-loan-does",
                topTxt: t("fmLoan.section_2.head"),
                subTxt: t("fmLoan.section_2.sub"),
                imgSrc: "/images/loan-guide/s-3.png",
              }}
            />
          </div>
          <div className="col-sm-3 center">
            <StepCard
              stepData={{
                link: "#fm-loan-requirements",
                topTxt: t("fmLoan.section_3.head"),
                subTxt: t("fmLoan.section_3.sub"),
                imgSrc: "/images/loan-guide/s-4.png",
              }}
            />
          </div>
        </div>
        <div className="row d-flex justify-content-center">
          <div className="col-sm-9 center mb-5">
            <a href="#apply-fm-loan" className="step-card">
              <div className="row">
                <div className="col-9 col-sm-12">
                  <h5>{t("fmLoan.section_4.head")}</h5>
                  <p>{t("fmLoan.section_4.sub")}</p>
                </div>
              </div>
            </a>
          </div>
        </div>
      </div>
      <WhatFannieMae />
      <WhatFMDoes />
      <FmRequirements />
      <ApplyFMLoan />
    </div>
  );
}

export default FannieMaeLoan;
