import React from "react";
import { useTranslation } from "react-i18next";

import QuoteSteps from "../../../QuoteSteps/QuoteSteps";

import "./ApplyFMLoan.css";

function ApplyFMLoan() {
  const { t } = useTranslation();
  return (
    <div
      id="apply-fm-loan"
      className="apply-fm-loan light-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-12 col-md-12 col-lg-12 mt-5">
            <QuoteSteps stepOneSub={t("fmLoan.section_4.txt_1")} />
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>{t("fmLoan.back_to_top")}</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ApplyFMLoan;
