import React from "react";
import { useTranslation } from "react-i18next";

import "./FmRequirements.css";

function FmRequirements() {
  const { t } = useTranslation();
  return (
    <div
      id="fm-loan-requirements"
      className="fm-loan-requirements dark-background-div pt-5 pb-5"
    >
      <div className="container">
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center">
            <img
              className="loan-details-img"
              src="/images/loan-guide/s-4.png"
              alt="/images/loan-guide/s-4.png"
            />
            <p className="header-text-3">{t("fmLoan.section_3.head")}</p>
            <h4 className="header-text-1">{t("fmLoan.section_3.sub")}</h4>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8">
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("fmLoan.section_3.txt_1")}
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("fmLoan.section_3.txt_2")}
            </p>
            <p className="sub-text-1 sub-text-margin-bottom">
              {t("fmLoan.section_3.txt_3")}
            </p>
          </div>
        </div>
        <div className="row d-flex justify-content-center mt-5 mb-5">
          <div className="col-sm-8 center mt-5">
            <a href="#top" className="back-top-btn">
              <p>↑</p>
              <p>{t("fmLoan.back_to_top")}</p>
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default FmRequirements;
